import React, { useState, useEffect } from 'react';
import './NavBar.css';
import {BrowserRouter as Router, Switch, Route, useLocation, NavLink} from 'react-router-dom';
import { FaAlignJustify } from 'react-icons/fa';







const Header = () =>{
    
    const [ isCheckbox, setCheckbox ] = useState("unchecked")
    const [ tranparent, setTranparent ] = useState("transparent")

    function handleClick () {
        if (isCheckbox === "unchecked") {
        setCheckbox("checked")
        } if (isCheckbox === "checked")
        setCheckbox("unchecked")
    }

    useEffect (() =>{
        document.addEventListener("scroll", () => {
            const backgroundcolor = window.scrollY < 100 ? "transparent" : "solid";
      
            setTranparent(backgroundcolor);
          });
    }, [])
    
    return(
<div>
    <div className={`container ${tranparent}`}>
        <div className="navBar">
           <FaAlignJustify onClick={handleClick} className={`checkbox ${tranparent}`}/>
            <h4> <NavLink to='/' className={`logo ${tranparent}`} >david haslett.</NavLink></h4>
                <ul className={isCheckbox} >
                    <li><NavLink to='/' className={`links ${tranparent}`} onClick={handleClick}> Home</NavLink></li>       
                    <li><NavLink to='/about-me' className={`links ${tranparent}`}  onClick={handleClick}>About Me</NavLink></li>       
                    <li><a href='https://drive.google.com/file/d/1IVZn9IRZS5zEU8R5beTyTE8BxymX88vq/view?usp=sharing' target="_blank" className={`links ${tranparent}`}  onClick={handleClick}>CV</a></li>       
                    <li><NavLink to='/contact' className={`links ${tranparent}`} onClick={handleClick}>Contact</NavLink></li>       
                </ul>
            
                
        </div>
    </div>

</div>              
    );
}

export default Header; 

